<template>
  <div>
    <h1>留言板</h1>
    <div>
      <div class="Bjq">
        <div class="editor_div">
          <div class="editor_toolbar">
            <div>
              <i class="iconfont icon-moon"></i>
              <span>{{name}}</span>
              <span v-if="!isLogin" @click="showlogin()" class="fr">登录</span>
            </div>
          </div>
          <div class="editor_textarea">
            <i-input type="textarea" placeholder="请输入..." v-model="sendvalue"></i-input>
          </div>
        </div>
         <Button v-if="isLogin" type="primary" @click="sendMsg()">提交</Button>
      </div>
    </div>
    <div class="CommentList">
      <List v-for="(item,index) in comments_list" :key="index">
        <div>
          <small>{{comments_list.length-index}}楼</small>
        </div>
        <ListItem>
          <ListItemMeta
            avatar="https://dev-file.iviewui.com/userinfoPDvn9gKWYihR24SpgC319vXY8qniCqj4/avatar"
            :title="item.username"
            :description="item.msg"
          />
        </ListItem>
      </List>
     <!-- <div style="text-align: center;">
        <button type="button" class="ant-btn">
          <span>加载更多</span>
        </button>
      </div>-->
    </div>
    <login-modal
      :loginModalProps="loginModalshow"
      v-on:closeLogin="closeLogin"
      v-on:changeLoginState="changeLoginState"
    ></login-modal>
  </div>
</template>

<script>
import Login_modal from "../components/login";
export default {
  name: "Comments",
  components: {
    "login-modal": Login_modal,
  },
  data() {
    return {
      loginModalshow: false,
      name: "先登录吧！",
      isLogin: false,
      comments_list: [],
      sendvalue: "",
    };
  },
  created() {
    this.getMsg();
  },
  methods: {
    closeLogin() {
      this.loginModalshow = false;
    },
    showlogin() {
      this.loginModalshow = true;
    },
    changeLoginState(res) {
      this.name = res.username;
      this.isLogin = true;
    },
    getMsg() {
      this.$api.ChatService.getMsg().then((res) => {
        this.comments_list = res.data.retValue;
      });
    },
    sendMsg() {
      const data ={msg:this.sendvalue,...JSON.parse(window.localStorage.userInfo)}
       this.$api.ChatService.sendMsg(data).then((res) => this.getMsg());
    },
  },
};
</script>

<style lang="less">
//  @import 'iview/dist/styles/iview.css';
@import "./comments.less";
@import "../assets/font.less";
.ivu-input-wrapper {
  height: 100%;
}
</style>


