<template>
  <div class="content">
    <name class="my-id">wwwlin</name>
    <div class="my-sort">
      <router-link class="link" to="/home/resume">简历</router-link>
      <router-link class="link" to="/home/comments">留言</router-link>
      <router-link class="link" to="/home/other">其他</router-link>
    </div>
    <div v-if="local" class="Gotop active">
      <div class="go-top-inner" @click="onTop()">
        <svg
          t="1565238236954"
          fill="currentColor"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="3644"
          width="200"
          height="200"
        >
          <path
            d="M483.875 230.75a28.125 28.125 0 0 1 56.25 0v590.625a28.125 28.125 0 0 1-56.25 0z"
            p-id="3645"
          />
          <path
            d="M512 242.28125l-205.03125 205.3125a28.125 28.125 0 1 1-39.9375-39.9375l225-225a28.125 28.125 0 0 1 39.9375 0l225 225a28.125 28.125 0 0 1-39.9375 39.9375z"
            p-id="3646"
          />
        </svg>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
let timer;
export default {
  name: "Layout",
  data() {
    return {
      msg: "this is my page",
      local: false,
    };
  },
  mounted: function () {
    window.addEventListener("scroll", this.btn_pos);
  },
  methods: {
    onTop() {
      cancelAnimationFrame(timer);
      timer = requestAnimationFrame(function fn() {
        var oTop =
          document.body.scrollTop || document.documentElement.scrollTop;
        if (oTop > 0) {
          document.body.scrollTop = document.documentElement.scrollTop =
            oTop - 50;
          timer = requestAnimationFrame(fn);
        } else {
          cancelAnimationFrame(timer);
        }
      });
    },
    btn_pos() {
      //滚动条的高度
      var scrollTop =
        document.body.scrollTop || document.documentElement.scrollTop;
      var clientHeight = 100;
      //滚动条的高度>可视区的高度
      if (scrollTop > clientHeight) {
        this.local = true;
      } else {
        this.local = false;
      }
    },
  },
  destroyed() {
    //离开这个界面之后，删除，不然会有问题
    window.removeEventListener("scroll", this.btn_pos);
  },
};
</script>

<style>
.content {
  width: 100%;
  text-align: center;
}
.link {
  color: #948c76;
  text-decoration: none;
}
.content .my-id {
  font-size: 1.3em;
  font-weight: 400;
  padding: 0 0.5em;
  letter-spacing: 0.1em;
  line-height: 2em;
  margin: 2em 0;
  display: inline-block;
  color: #948c76;
  background: #fdf4e8;
}
.content .my-sort {
  margin-bottom: 2em;
}
.Gotop {
  right: 10%;
  bottom: 10%;
  width: 45px;
  height: 45px;
  line-height: 45px;
  border-radius: 50%;
  position: fixed;
  cursor: pointer;
  text-align: center;
  transition: all 0.3s;
  z-index: 1000;
  opacity: 1;
  display: none;
  background: #ffffff;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 4px -1px,
    rgba(0, 0, 0, 0.14) 0px 4px 5px 0px, rgba(0, 0, 0, 0.12) 0px 1px 10px 0px;
  color: #787978;
}
.Gotop.active {
  display: block;
  animation: fadeTo 0.7s forwards;
}
.Gotop .go-top-inner {
  width: 100%;
  height: 100%;
  position: relative;
}
.Gotop img,
.Gotop svg {
  width: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
svg:not(:root) {
  overflow: hidden;
}
</style>



